import React from 'react'
import './index.scss'
import { List, Icon, Modal } from 'antd-mobile'
import { getMusicUrl, getMusicLyc } from '../../api/music'
import { getSingerDesc } from '../../api/singer'
import { playAnimation } from '../../utils/playAnimation'
import store from '../../redux'
import myToast from '../../utils/myToast'

const Item = List.Item
const Brief = Item.Brief

export default class playMusic extends React.Component {
  state = {
    isPlay: false,
    musicUrl: '',
    show: 0,
    musicList: [],
    index: 0,
    picUrl: '',
    musicName: '',
    musicAr: '',
    playLength: {
      current: 0,
      end: 0
    },
    platPattern: 'listCycle',
    lycArr: null,
    currentLyric: null,
    isShowLyc: false,
    popupShow: false,
    isTouchDown: false
  }
  componentDidMount() {
    window.addEventListener('touchmove', event => {
      if (!this.state.isTouchDown) {
        return
      }
      var x = event.changedTouches[0].clientX - this.progressBox.offsetLeft
      this.setProgress(x)
    })
    store.musicUrl.subscribe(() => {
      let data = store.musicUrl.getState()
      playAnimation(data.even)
      this.setState({ musicList: data.list }, () => {
        this.setState({ index: data.index }, () => {
          this.setMusicUrl()
        })
      })
    })
  }
  //把歌词文本处理成合适的数组
  lyricDispose(lyric) {
    let arr = lyric.split('\n') //把歌词用/n分割成数组
    let lyricArr = []
    for (let i = 0; i < arr.length; i++) {
      if (!arr[i]) break
      let timeStr = arr[i].match(/\[.*\]/g)[0] //获取时间字符串
      timeStr = timeStr.substring(1, timeStr.length - 1)
      let str = arr[i].replace(/\[.*\]/g, '') //截取歌词字符串
      let timeArr = timeStr.split('][') //截取时间字符串,并且把同一项的多个时间分割成数组
      for (let j = 0; j < timeArr.length; j++) {
        //遍历时间数组
        lyricArr.push({
          time: Number(timeArr[j].split(':')[1]) + Number(timeArr[j].split(':')[0]) * 60,
          str
        }) //把每一个时间和对应的歌词push到数组中
      }
    }
    return lyricArr.sort((a, b) => {
      return a.time - b.time
    }) //把数组按照时间大小排序并返回
  }
  //设置音乐url
  async setMusicUrl() {
    this.setState({ musicUrl: '' })
    this.progress.style.width = ''
    try {
      var res = await getMusicUrl(this.state.musicList[this.state.index].id)
      if (res.data[0].url === null) {
        myToast(`歌曲《${this.state.musicList[this.state.index].name}》暂无版权,播放失败!`, 2000)
        this.MusicChange(1)
      }
      this.setState({ musicUrl: res.data[0].url }, () => {
        if (this.state.show === 0) {
          this.setState({ show: 60 })
          this.audio.play()
        }
      })
      this.getSingerDesc(this.state.musicList[this.state.index].artId)
      this.setState({
        musicName: this.state.musicList[this.state.index].name
      })
      this.setState({
        musicAr: this.state.musicList[this.state.index].arName
      })
      let lyc = await getMusicLyc(this.state.musicList[this.state.index].id)
      if ('lrc' in lyc) {
        this.setState({ lycArr: this.lyricDispose(lyc.lrc.lyric) })
      } else {
        this.setState({ lycArr: '' })
      }
    } catch (err) {
      console.log(err)
    }
  }
  // 点击播放或暂停按钮
  Playing(_this) {
    let audio = document.getElementById('audio')
    if (_this.state.isPlay) {
      audio.pause()
    } else {
      audio.play()
    }
  }
  // 显示详细播放模块
  showAll(_this) {
    _this.setState({ show: '100%' })
    let tool = document.getElementsByClassName('tool')[0]
    tool.style.display = 'none'
  }
  // 隐藏详细播放模块
  unShowAll(_this) {
    _this.setState({ show: 60 })
    let tool = document.getElementsByClassName('tool')[0]
    tool.style.display = 'flex'
    tool.style.opacity = 0
    setTimeout(() => {
      tool.style.opacity = 1
    }, 0)
  }
  //当音乐播放时
  timeupdate(_this) {
    let audio = document.getElementById('audio')
    // 是否播放结束
    if (audio.ended && this.state.platPattern !== 'singleCycle') {
      this.MusicChange(1)
      return
    }
    let currentTime = '0:00'
    let duration = '0:00'
    if (audio.currentTime > 0) {
      currentTime = Math.floor(audio.currentTime)
      let m = Math.floor(currentTime / 60).toString()
      let s = (currentTime % 60).toString()
      currentTime = `${m}:${s.padStart(2, '0')}`
    }
    if (audio.duration > 0) {
      duration = Math.floor(audio.duration)
      let m = Math.floor(duration / 60).toString()
      let s = (duration % 60).toString()
      duration = `${m}:${s.padStart(2, '0')}`
    }
    _this.setState({
      playLength: {
        current: currentTime,
        end: duration
      }
    })
    if (!this.state.isTouchDown) {
      let percentage = audio.currentTime / audio.duration
      this.progress.style.width = percentage * this.progressBox.offsetWidth + 'px'
    }
    if (this.state.lycArr) {
      // 调整歌词显示
      let current = audio.currentTime + 0.3 //获取当前播放时间
      for (let i = this.state.lycArr.length - 1; i >= 0; i--) {
        //倒序遍历歌词数组
        if (this.state.lycArr[i].time < current) {
          if (this.state.currentLyric !== i) {
            //数组下标i的时间小于当前时间时,设置当前显示的歌词为歌词数组的下标i
            this.lycBrief.innerText = this.state.lycArr[i].str
            this.allLyc.style.top = -i * 50 + 'px'
          }
          this.setState({ currentLyric: i }) //设置当前显示的歌词下标
          return //退出循环
        }
      }
      // 如果上面循环未有结果,设置当前显示歌词为空,歌词滚动到初始位置
      this.setState({ currentLyric: null })
      this.allLyc.style.top = '50px'
    }
  }
  // 获取歌手详情设置专辑封面
  async getSingerDesc(id) {
    try {
      let res = await getSingerDesc(id)
      this.setState({
        picUrl: res.artist.picUrl
      })
    } catch (err) {
      console.log(err)
    }
  }
  //单首音乐播放完毕
  MusicChange(index) {
    var _index = null
    switch (this.state.platPattern) {
      case 'listCycle':
      case 'singleCycle': {
        if (index === 1) {
          if (this.state.index !== this.state.musicList.length - 1) {
            _index = this.state.index + 1
          } else {
            _index = 0
          }
        }
        if (index === -1) {
          if (this.state.index !== 0) {
            _index = this.state.index - 1
          } else {
            _index = this.state.musicList.length - 1
          }
        }
        this.setState({ index: _index }, () => {
          this.setMusicUrl()
        })
        break
      }
      case 'random': {
        _index = Math.floor(Math.random() * this.state.musicList.length)
        this.setState({ index: _index }, () => {
          this.setMusicUrl()
        })
        break
      }
      default: {
        break
      }
    }
  }
  // 显示音乐列表弹出层
  showPopup() {
    this.setState(
      {
        popupShow: !this.state.popupShow
      },
      () => {
        if (this.state.popupShow) {
          this.popupBox.scrollTop = this.state.index * 40
        }
      }
    )
  }
  // 点击进度条
  progressClick(e) {
    this.setProgress(e.clientX - this.progressBox.offsetLeft)
    this.setMucTiem()
  }
  setProgress(x) {
    this.progress.style.width = x + 'px'
  }
  setMucTiem() {
    if (!this.state.isPlay) {
      return
    }
    let percentage = this.progress.offsetWidth / this.progressBox.offsetWidth
    if (percentage < 0.04) {
      percentage = 0
    }
    this.audio.currentTime = this.audio.duration * percentage
  }
  render() {
    return (
      <div className='playMusic' style={{ bottom: this.state.show }}>
        <div className='bg'>
          <div className='before' style={{ backgroundImage: `url(${this.state.picUrl})` }}></div>
        </div>
        <div className='tool'>
          <Item thumb={this.state.picUrl} onClick={() => this.showAll(this)} ref={val => (this.img = val)}>
            {this.state.musicName} <Brief>{this.state.musicAr}</Brief>
          </Item>
          <div className='btn'>
            <i
              onClick={() => this.Playing(this)}
              className={this.state.isPlay ? 'iconfont icon-zanting' : 'iconfont icon-bofang'}
            />
            <i className='iconfont icon-yinleliebiao' onClick={() => this.showPopup()} />
          </div>
        </div>
        <div className='all'>
          <Icon type='down' onClick={() => this.unShowAll(this)} />
          <div className='title'>{this.state.musicName}</div>
          <div className='singer'>{this.state.musicAr}</div>
          <div className='lycBox' onClick={() => this.setState({ isShowLyc: !this.state.isShowLyc })}>
            <div
              className='plate'
              style={{
                width: window.screen.width * 0.8,
                height: window.screen.width * 0.8,
                opacity: this.state.isShowLyc ? 0 : 1
              }}
              ref={val => (this.plate = val)}
            >
              <img src={this.state.picUrl} alt='' />
            </div>
            <div className='lycBrief' style={{ opacity: this.state.isShowLyc ? 0 : 1 }}>
              <p ref={val => (this.lycBrief = val)}></p>
            </div>
            <div className='allLyc' style={{ opacity: this.state.isShowLyc ? 1 : 0 }} ref={val => (this.allLyc = val)}>
              {this.state.lycArr &&
                this.state.lycArr.map((item, index) => {
                  return (
                    <p key={index} className={index === this.state.currentLyric ? 'current' : ''}>
                      {item.str}
                    </p>
                  )
                })}
            </div>
          </div>
          <div className='allTool'>
            <div className='progressBar'>
              <span>{this.state.playLength.current}</span>
              <div
                className='progressBox'
                onTouchStart={() => this.setState({ isTouchDown: true })}
                onTouchEnd={() => {
                  setTimeout(() => {
                    this.setMucTiem()
                    this.setState({ isTouchDown: false })
                  }, 0)
                }}
                onClick={e => this.progressClick(e)}
              >
                <div ref={val => (this.progressBox = val)}>
                  <div className='progress' ref={val => (this.progress = val)}></div>
                </div>
              </div>
              <div className='audio'>
                <audio
                  autoPlay
                  onTimeUpdate={() => this.timeupdate(this)}
                  controls
                  src={this.state.musicUrl}
                  onPlaying={() => {
                    this.setState({ isPlay: true })
                  }}
                  onPause={() => {
                    this.setState({ isPlay: false })
                  }}
                  id='audio'
                  loop={this.state.platPattern === 'singleCycle'}
                  ref={val => (this.audio = val)}
                />
              </div>
              <span>{this.state.playLength.end}</span>
            </div>
            <div className='control'>
              {this.state.platPattern === 'listCycle' && (
                <i
                  className='iconfont icon-icon--'
                  onClick={() => {
                    this.setState({
                      platPattern: 'singleCycle'
                    })
                    myToast('单曲循环', 2000)
                  }}
                />
              )}
              {this.state.platPattern === 'singleCycle' && (
                <i
                  className='iconfont icon-danquxunhuan'
                  onClick={() => {
                    this.setState({
                      platPattern: 'random'
                    })
                    myToast('随机播放', 2000)
                  }}
                />
              )}
              {this.state.platPattern === 'random' && (
                <i
                  className='iconfont icon-suijibofang'
                  onClick={() => {
                    this.setState({
                      platPattern: 'listCycle'
                    })
                    myToast('顺序播放', 2000)
                  }}
                />
              )}
              <i className='iconfont icon-shangyishoushangyige' onClick={() => this.MusicChange(-1)} />
              <i
                onClick={() => this.Playing(this)}
                className={this.state.isPlay ? 'iconfont icon-zanting' : 'iconfont icon-bofang'}
              />
              <i className='iconfont icon-xiayigexiayishou' onClick={() => this.MusicChange(1)} />
              <i className='iconfont icon-yinleliebiao' onClick={() => this.showPopup()} />
            </div>
          </div>
        </div>
        <Modal popup visible={this.state.popupShow} onClose={() => this.showPopup()} animationType='slide-up'>
          <div className='songListPopup'>
            <List
              renderHeader={() => (
                <div className='title'>
                  <div className='left'>
                    {this.state.platPattern === 'listCycle' && (
                      <>
                        <i
                          className='iconfont icon-icon--'
                          onClick={() => {
                            this.setState({
                              platPattern: 'singleCycle'
                            })
                          }}
                        />
                        顺序播放
                      </>
                    )}
                    {this.state.platPattern === 'singleCycle' && (
                      <>
                        <i
                          className='iconfont icon-danquxunhuan'
                          onClick={() => {
                            this.setState({
                              platPattern: 'random'
                            })
                          }}
                        />
                        单曲循环
                      </>
                    )}
                    {this.state.platPattern === 'random' && (
                      <>
                        <i
                          className='iconfont icon-suijibofang'
                          onClick={() => {
                            this.setState({
                              platPattern: 'listCycle'
                            })
                          }}
                        />
                        随机播放
                      </>
                    )}
                  </div>
                  <i className='iconfont icon-guanbi' onClick={() => this.showPopup()} />
                </div>
              )}
              className='popup-list'
            >
              <div className='box' ref={val => (this.popupBox = val)}>
                <div>
                  {this.state.musicList.map((item, index) => (
                    <Item
                      key={index}
                      onClick={() =>
                        this.setState({ index }, () => {
                          this.setMusicUrl()
                        })
                      }
                    >
                      <span style={{ color: this.state.index === index ? '#d4473e' : '#a09191' }}>
                        <i
                          className='iconfont icon-bofang'
                          style={{
                            opacity: this.state.index === index ? 1 : 0
                          }}
                        />
                        {item.name} - {item.arName}
                      </span>
                      <div className='ico'>
                        <i className='iconfont icon-shoucang' />
                        <i className='iconfont icon-RectangleCopy' />
                      </div>
                    </Item>
                  ))}
                </div>
              </div>
            </List>
          </div>
        </Modal>
      </div>
    )
  }
}
