import React from 'react';
import './index.scss';
import { NavBar, Icon, List, Button } from 'antd-mobile';
import { getDetails } from '../../api/song_menu';
import store from '../../redux';
import {setMucData} from '../../redux/actionCreator';
const Item = List.Item;
const Brief = Item.Brief;

export default class songMenu extends React.Component {
    state = {
        data: {
            name: '歌单',
            tracks: [],
            subscribedCount: 0,
            coverImgUrl:
                'http://img3.imgtn.bdimg.com/it/u=1105085267,629346802&fm=26&gp=0.jpg'
        },
        bgStyle: {
            backgroundImage:
                'url("http://img3.imgtn.bdimg.com/it/u=1105085267,629346802&fm=26&gp=0.jpg")'
        },
        show: {}
    };
    async componentDidMount() {
        this.setState({ show: { transform: 'rotate(0deg)' } });
        document.documentElement.scrollTop = 0;
        try {
            let res = await getDetails(this.props.match.params.id);
            this.setState({ data: res.playlist });
            this.setState({
                bgStyle: {
                    backgroundImage: `url("${res.playlist.coverImgUrl}")`
                }
            });
        } catch (err) {
            console.log(err);
        }
    }
    // 播放音乐
    play(list, index, e) {
        let arr = [];
        list.forEach(item => {
            arr.push({
                id: item.id,
                name: item.name,
                artId: item.ar[Math.floor(Math.random() * item.ar.length)].id,
                arName: item.ar[0].name
            });
        });
        store.musicUrl.dispatch(setMucData({ list: arr, index, even: e }))
    }
    render() {
        return (
            <div
                id="songMenu"
                className="songMenu"
                ref={val => (this.songMenu = val)}
                style={this.state.show}
            >
                <div className="cover">
                    <div className="bg" style={this.state.bgStyle}></div>
                    <div className="content">
                        <NavBar
                            mode="light"
                            icon={<Icon type="left" />}
                            onLeftClick={() => {
                                this.setState({
                                    show: { transform: 'rotate(-90deg)' }
                                });
                                setTimeout(() => {
                                    this.props.history.go(-1);
                                }, 400);
                            }}
                            leftContent={
                                <span className="title">
                                    {this.state.data.name}
                                </span>
                            }
                        ></NavBar>
                        <div className="synopsis">
                            <img src={this.state.data.coverImgUrl} alt="" />
                            <div>
                                <div className="title">
                                    {this.state.data.name}
                                </div>
                                <div className="text">
                                    <img
                                        src="http://p1.music.126.net/QWMV-Ru_6149AKe0mCBXKg==/1420569024374784.jpg"
                                        alt=""
                                    />
                                    网易云音乐
                                </div>
                            </div>
                        </div>
                        <div className="praise">
                            <ul>
                                <li>
                                    <i className="iconfont icon-wodefankui"></i>
                                    <span>评论</span>
                                </li>
                                <li>
                                    <i className="iconfont icon-shoucang"></i>
                                    <span>点赞</span>
                                </li>
                                <li>
                                    <i className="iconfont icon-add"></i>
                                    <span>收藏</span>
                                </li>
                                <li>
                                    <i className="iconfont icon-icon_threeline_fill"></i>
                                    <span>更多</span>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div id="songMenuList" className="songMenuList">
                    <List>
                        <div className="title">
                            <div>
                                <i
                                    className="iconfont icon-bofang"
                                    onClick={e =>
                                        this.play(this.state.data.tracks, 0, e)
                                    }
                                ></i>
                                <span>
                                    播放全部
                                    <span>
                                        {' '}
                                        (共{this.state.data.tracks.length}
                                        首)
                                    </span>
                                </span>
                            </div>
                            <Button>
                                <i className="iconfont icon-add"></i> 收藏
                                <span className="num">
                                    (
                                    {Math.ceil(
                                        this.state.data.subscribedCount / 1000
                                    ) / 10}
                                    万)
                                </span>
                            </Button>
                        </div>
                        {this.state.data.tracks.map((item, index) => {
                            return (
                                <div className="box" key={index}>
                                    <span className="index">{index + 1}</span>
                                    <Item
                                        multipleLine
                                        onClick={e =>
                                            this.play(
                                                this.state.data.tracks,
                                                index,
                                                e
                                            )
                                        }
                                    >
                                        {item.name}{' '}
                                        <Brief>{item.ar[0].name}</Brief>
                                    </Item>
                                </div>
                            );
                        })}
                    </List>

                    {this.state.data.tracks.length === 0 && (
                        <Icon type="loading" />
                    )}
                </div>
            </div>
        );
    }
}
