import request from '../utils/request';

function getHotSongMenu() {
    return request({
        method: 'get',
        url: '/personalized'
    });
}

function getDetails(id) {
    return request({
        method: 'get',
        url: '/playlist/detail',
        params:{id}
    });
}

function getPlaylist(id) {
    return request({
        method: 'get',
        url: '/simi/playlist',
        params:{id}
    });
}


export {
    getHotSongMenu,
    getDetails,
    getPlaylist
}