import request from '../utils/request';

function searchMusic(keywords) {
    return request({
        method: 'get',
        url: '/search',
        params:{
            keywords
        }
    });
}


function getSearchHot() {
    return request({
        method: 'get',
        url: '/search/hot'
    });
}

export {
    getSearchHot,
    searchMusic
}